@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
* {
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.kave {
  &-btn {
    position: relative;
    border: solid 1px rgba(255, 255, 0, 0.5);
    background: none;
    color: rgba(255, 255, 0, 0.7);
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    font-weight: normal;
    letter-spacing: 1.8px;
    width: 200px;
    height: 70px;
    padding: 0;
    transition: all 0.25s ease;
    outline: none;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 10%;
      width: 60px;
      height: 1px;
      transform: translateY(-1px);
      background: rgba(255, 255, 0, 1);
      transition: all 0.25s ease;
    }
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 10%;
      width: 60px;
      height: 1px;
      transform: translateY(1px);
      background: rgba(255, 255, 0, 1);
      transition: all 0.25s ease;
    }
    &:hover {
      box-shadow: 1px 1px 8px rgba(0, 214, 252, 0.3);
      color: white;
      background-color: transparent;
      text-shadow: 0 0 8px white;
      &.kave-btn:before {
        left: 0;
        width: 20px;
      }
      &.kave-btn:after {
        right: 0;
        width: 20px;
      }
      .kave-line:before {
        bottom: 0;
      }
      .kave-line:after {
        top: 0;
      }
    }
  }
  &-line {
    &:before {
      content: "";
      position: absolute;
      bottom: 30%;
      right: 0;
      width: 1px;
      height: 20px;
      transform: translateX(1px);
      background: rgba(0, 214, 252, 1);
      transition: all 0.25s ease;
    }
    &:after {
      content: "";
      position: absolute;
      top: 30%;
      left: 0;
      width: 1px;
      height: 20px;
      transform: translateX(-1px);
      background: rgba(0, 214, 252, 1);
      transition: all 0.25s ease;
    }
  }
}
